.navigation {
  position: fixed;
  width: 100%;
  @include itemCenter;
  background: $blue1;
  color: $white1;
  z-index: 1000;

  nav {
    position: relative;
    padding: 10px;
    width: 100%;
    height: 75px;
    border-bottom: 2px solid $blue2;
    display: grid;
    grid-template-columns: 20% 65% 15%;
    align-items: center;
    justify-content: center;
    z-index: 200;


    @media screen and (max-width: 850px) {
      width: 100%;
      display: flex;
      justify-content: space-between;

    }

    .name {
      text-align: left;
      padding-left: 10px;

      h3 {
        margin-bottom: 5px;
        font-weight: 700;
        font-size: 1rem;
        padding-left: 5px;
        color: $white1;
      }
    }

    .nav-item {
      position: relative;
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: start;
      align-items: center;
      color: $blue2;

      .nav-active {
        color: $white1;

        @media screen and (max-width: 850px) {
          color: $blue1;
        }
      }

      @media screen and (max-width: 850px) {
        position: absolute;
        color: $blue2;
        width: 100%;
        justify-content: center;
        left: 50%;
        top: 75px;
        transform: translate(-50%);
        background: $blue1;

      }

      li {
        padding: 7px;
        margin: 5px;
        font-size: 1.1rem;
        font-weight: 700;
        transition: 0.5s ease-in-out;

        &:hover {
          color: $white1;
          text-shadow: 5px 5px 35px $white1;

          // @media screen and (max-width: 850px) {
 //   color: $blue1;
 // }
        }
      }
    }
  }
}