.header {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  background: $white1;
  font-family: $font1;
  text-align: center;
  padding: 0 10px;
  @include itemCenter;
  // align-items: flex-end;

  .content {
    z-index: 100;


    h1 {
      margin-top: 75px;
      // font-size: 2.8rem;
      font-size: calc(1rem + 3vw);
      color: $blue1;
      cursor: grab;
    }

    h2 {
      padding: 10px 0;
      font-size: calc(1rem + 2vw);
      color: $red1;
    }

    p {
      font-size: calc(0.7rem + 0.7vw);
      padding: 10px 0;
    }

    .btn {
      padding: 20px 0;

      @include itemCenter;

      ul {
        padding: 0;
        display: flex;
        color: $white1;

        @media screen and (max-width: 550px) {
          flex-direction: column;
        }

        a {
          background: $blue2;
          margin: 10px;
          padding: 10px 50px;
          border-radius: 10px;
          box-shadow: 1px 3px 10px #ccc;
          transition: 0.5s ease-in-out;

          &:hover {
            transform: translate(10px, 5px);
          }
        }

        a:nth-child(2) {
          background: none;
          border: 1px solid $blue1;
          color: $blue1;
        }

        li {
          width: 100%;
          font-size: 1.2rem;

          @media screen and (max-width: 768px) {
            font-size: 1rem;
          }
        }
      }
    }
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100px;
    bottom: -85px;
    left: 0;
    background: $blue2;
    animation: border-transform 10s linear infinite alternate forwards;
    position: fixed;
  }

  @keyframes border-transform {

    0% {
      border-radius: 50% 65% 54% 46% / 30% 35% 54% 45%;
      background: $blue1;
    }

    14% {
      border-radius: 90% 60% 54% 46% / 50% 90% 40% 51%;
      background: $blue2;
    }

    28% {
      border-radius: 54% 46% 38% 62% / 49% 70% 30% 51%;
      background: $white1;
    }

    42% {
      border-radius: 61% 39% 55% 45% / 61% 38% 62% 39%;
      background: $red1;
    }

    56% {
      border-radius: 61% 32% 67% 33% / 40% 50% 50% 30%;
      background-color: $white2;
    }

    70% {
      border-radius: 50% 50% 34% 66% / 56% 68% 32% 44%;
      background: $blue2;
    }

    84% {
      border-radius: 46% 54% 50% 50% / 35% 61% 39% 65%;
      background: $blue1
    }

    100% {
      border-radius: 30% 65% 54% 46% / 30% 35% 54% 45%;
      background: $red1;
    }
  }
}