@font-face {
  font-family: "Ubuntu";
  src: url(../assets/fonts/Ubuntu-Medium.ttf);
}

@font-face {
  font-family: "Rajdhani";
  src: url(../assets/fonts/Rajdhani-Medium.ttf);
}

$font1 : Rajdhani;
$font2 : RussoOne;

$blue1 : #080E29;
$blue2: rgb(0, 196, 220);
$white1 : #fff;
$white2: rgb(241,
  241,
  241);
$red1 : #A90732;

* {
  margin: 0;
  margin: 0;
  box-sizing: border-box;

  &::selection {
    background: $blue2;
  }
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: black;
  background: $white1;
  color: $blue1;
}

li {
  list-style-type: none;
}

a,
a:visited {
  color: inherit;
  text-decoration: none;
}

@mixin itemCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}