.icons-display {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;


  .container {
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 50% 50%;


    .left-collection {
      width: 100%;
      height: 100vh;
      display: flex;
      align-items: flex-end;
      animation: anim infinite 1.2s linear;


      .content {
        padding: 10px 20px;
        width: 50%;
        height: 50vh;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        align-items: flex-end;

        .ic {
          width: 40px;
          height: 40px;
          padding: 20px;
          margin: 10px;
          border-radius: 15px;
          background: #ccc;
          display: flex;
          justify-content: center;
          align-items: center;
          opacity: 1;
          box-shadow: 1px 3px 10px #ccc;
          cursor: grab;

        }

        .ic-1 {
          background: url(../../../public/assets/icons/bootstrap.svg) center/cover;
        }

        .ic-2 {
          background: url(../../../public/assets/icons/css.svg) center/cover;
        }

        .ic-3 {
          background: url(../../../public/assets/icons/github.svg) center/cover;
          width: 20px;
          height: 20px;
        }

        .ic-4 {
          background: url(../../../public/assets/icons/html.svg) center/cover;
        }

        .ic-5 {
          background: url(../../../public/assets/icons/js.svg) center/cover;
        }

        .ic-6 {
          background: url(../../../public/assets/icons/mongo.svg) center/cover;
        }
      }

      @keyframes anim {

        50% {
          transform: translateY(3px);
        }

        100% {
          transform: translateY(0);
        }

      }
    }

    .right-collection {
      width: 100%;
      height: 100vh;
    }
  }

}