.social-network {
  display: flex;
  justify-content: flex-end;

  ul {
    padding: 0;
    margin: 0;
    width: 100%;
    width: 100px;
    display: flex;

    a {
      padding: 5px;
      transition: 0.5s ease-in-out;

      &:hover i {
        color: $white1;
      }

      i {
        font-size: 1.4rem;
        color: gray;
        transition: 0.5s ease-in-out;
      }
    }
  }
}